<template>
  <div id="app">
    <component :is="layout">
      <!-- @контент меняется от условия layout()@ -->
      <!-- <router-view/> @контент меняется от адресной строки@ Наличие имени у <router-view> определяет отображение компонента с соответствующим именем из опции components сопоставленного маршрута. -->
    </component>
  </div>
</template>

<script>
import EmptyLayout from '@/layouts/EmptyLayout';
import MainLayout from '@/layouts/MainLayout';

export default {
  components: {
    EmptyLayout, MainLayout, // импортируем для тега <component :is="layout">
  },
  computed: {
    layout() {
      return `${this.$route.meta.layout || 'Empty'}-Layout`; // возврашаяет состояние <router-view/> свойство и имя
    },
  },
};
</script>


